// event
import EventDispatcher from './EventDispatcher';
import WheelEvents from './events/WheelEvents';

// /**
//  * new  Symbol
//  * @type {Symbol}
//  * @private
//  */
// const singletonSymbol = Symbol('Scroll singleton symbol');
// /**
//  * singleton instance, nullable
//  * @type {?Wheel}
//  * @private
//  */
// let instance = null;

/**
 * mousewheel event 
 * <p>singleton  new  factory  instance </p>
 *
 * event handler  `events`  {@link WheelEvents} instance 
 * - events.moved > 0 - wheel up
 *
 * ```
 * const up = (events) => {
 *  // wheel up
 * };
 * const down = (events) => {
 *  // wheel down
 * };
 * const update = (events) => {
 *  // wheel up / down
 * };
 * const wheel = new Wheel();
 * wheel.threshold = 500;
 * wheel.on(Wheel.UP, up);
 * wheel.on(Wheel.DOWN, down);
 * wheel.on(Wheel.UPDATE, update);
 * wheel.start();
 * ```
 */
export default class Wheel extends EventDispatcher {
  // ----------------------------------------
  // EVENT
  // ----------------------------------------
  /**
   * wheel up  - wheelUp
   * @type {string}
   */
  static UP = 'wheelUp';

  /**
   * wheel down  - wheelDown
   * @type {string}
   */
  static DOWN = 'wheelDown';

  /**
   * wheel move  - wheelUpdate
   * @type {string}
   */
  static UPDATE = 'wheelUpdate';

  // // ----------------------------------------
  // // STATIC METHOD
  // // ----------------------------------------
  // /**
  //  * Wheel instance  singleton 
  //  * @returns {Wheel} Wheel instance 
  //  */
  // static factory() {
  //   if (instance === null) {
  //     instance = new Wheel(singletonSymbol);
  //   }
  //   return instance;
  // }
  // ---------------------------------------------------
  //  CONSTRUCTOR
  // ---------------------------------------------------
  // /**
  //  * singleton 
  //  * @param {Symbol} checkSymbol singleton  private instance
  //  * @returns {Wheel} singleton instance 
  //  */
  /**
   * wheel event 
   * @param {number} [threshold=200]  - event (px)
   */
  constructor(threshold = 200) {
    // // checkSymbol  singleton 
    // if (checkSymbol !== singletonSymbol) {
    //   throw new Error('don\'t use new, instead use static factory method.');
    // }
    // // instance  instance  null  this 
    // if (instance !== null) {
    //   return instance;
    // }
    super();
    // onetime setting
    // instance = this;

    // event handler
    // const onMouseWheel = this.onMouseWheel.bind(this);
    /**
     * bound onMouseWheel
     * @type {function}
     */
    this.onMouseWheel = this.onMouseWheel.bind(this);
    // this.onMouseWheel = () => onMouseWheel;
    /**
     * , wheel 
     * @type {number}
     * @default 200
     */
    this.threshold = threshold;
    /**
     * wheelDelta 
     * @type {number}
     */
    this.moved = 0;
    // /**
    //  * start flag
    //  * @type {boolean}
    //  */
    // this.started = false;
    // const events = {
    //   up: new WheelEvents(Wheel.UP, this),
    //   down: new WheelEvents(Wheel.DOWN, this),
    // };
    /**
     * UP / DOWN Events instance
     * @returns {{up: WheelEvents, down: WheelEvents, update: WheelEvents}} UP / DOWN Events instance
     */
    this.events = {
      up: new WheelEvents(Wheel.UP, this, this),
      down: new WheelEvents(Wheel.DOWN, this, this),
      update: new WheelEvents(Wheel.UPDATE, this, this),
    };

    //  instance 
    // return this;
  }

  // // ----------------------------------------
  // // EVENT
  // // ----------------------------------------
  // /**
  //  * wheel up 
  //  * @event UP
  //  * @returns {string} event, wheelUp 
  //  * @default wheelUp
  //  */
  // static get UP() {
  //   return 'wheelUp';
  // }
  // /**
  //  * wheel  
  //  * @event DOWN
  //  * @returns {string} event, wheelUp 
  //  * @default wheelUp
  //  */
  // static get DOWN() {
  //   return 'wheelDown';
  // }
  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   *  property  `0` 
   */
  reset() {
    this.moved = 0;
  }

  /**
   * mousewheel event 
   * @returns {Wheel} method chain  instance 
   */
  start() {
    // if (this.started) {
    //   return this;
    // }
    // this.started = true;
    // this.unwatch();
    this.stop();
    this.reset();
    window.addEventListener('wheel', this.onMouseWheel, false);
    return this;
  }

  /**
   * mousewheel event 
   * @returns {Wheel} method chain  instance 
   */
  stop() {
    // if (!this.started) {
    //   return this;
    // }
    // this.started = false;
    window.removeEventListener('wheel', this.onMouseWheel);
    return this;
  }

  /**
   * window mousewheel event handler
   * <p>delta  `this.moving` </p>
   *
   * @listens {WheelEvent} WheelEvent.wheel
   * @param {WheelEvent} event window wheel event
   * @returns {number}  delta  
   */
  onMouseWheel(event) {
    const { deltaY } = event;
    return this.moving(deltaY);
  }

  /**
   * mouse delta 
   * @param {number} delta mouse delta 
   * @returns {number}  delta  
   */
  moving(delta) {
    /**
     * 
     * @type {number}
     */
    this.moved += delta;
    // @type {number}
    const { moved } = this;

    // 0 check
    if (moved === 0) {
      //  0 
      return moved;
    }

    // 
    if (Math.abs(moved) >= this.threshold) {
      // scroll event 
      this.update(moved);
      // down / up
      if (moved > 0) {
        // scroll up
        this.up(moved);
      } else {
        this.down(moved);
      }

      // initialize moved, 
      // this.moved = 0;
      this.reset();
      return moved;
    }
    // 
    return moved;
  }

  /**
   * scroll up 
   * @param {number} moved 
   * @returns {number} 
   */
  up(moved) {
    // @type {Events}
    const events = this.events.up;
    events.moved = moved;
    this.dispatch(events);

    return moved;
  }

  /**
   * scroll down 
   * @param {number} moved 
   * @returns {number} 
   */
  down(moved) {
    // @type {Events}
    const events = this.events.down;
    events.moved = moved;
    this.dispatch(events);

    return moved;
  }

  /**
   * scroll update 
   * @param {number} moved 
   * @returns {number} 
   */
  update(moved) {
    // @type {Events}
    const events = this.events.update;
    events.moved = moved;
    this.dispatch(events);

    return moved;
  }
}
